        created(){
            this.shopid = JSON.parse(localStorage.getItem("shop")).id;

            //获取餐馆详情信息
            this.axios.get("shopping/restaurant/" + this.shopid).then(res=>{
                console.log(res);
                this.shopDetail = res;
            }).catch(err=>{});
        }
    }
</script>

